import React from 'react';
import cl from './FormButton.module.scss';

function FormButton(props) {

    const { children, disabled } = props

    return (
        <div className={cl.button_wrapper}>
            <button
                className={`${cl.button} button`}
                type='submit'
                disabled={disabled}
                {...props}
            >
                {children}
            </button>
        </div>
    )
}

export default FormButton