import React from 'react';
import { Field, ErrorMessage } from 'formik';
import cl from './FormInput.module.scss';


function FormInput(props) {
    const { label, name, placeholder, hasError, value, touched, ...rest } = props

    return (
        <div
            className={`${cl.input_main_wrapper} input_main_wrapper`}
        >

            <div
                className={cl.input_field}
            >
                <label
                    htmlFor={name}
                    className={`${`${cl.label} label`} ${value && `${cl.label_active} label_active`}`}
                >
                    {placeholder}
                </label>
                <Field
                    className={`${`${cl.input} input`} ${hasError ? `${cl.has_errors} has_errors` : touched && `${cl.correct} correct`}`}
                    as="input"
                    id={name}
                    name={name}
                    {...rest}
                />
            </div>

            <span className={`${cl.red_message} red_message`}>
                <ErrorMessage name={name} />
            </span>

        </div>
    )
}

export default FormInput;